export class UserDetails extends React.Component {
    constructor(props) {
        super(props);
        this.state = { user: { id: 0, name: '', img: '' } };
    }

    componentDidMount() {
        this.setUserState();
    }

    componentWillUpdate(nextProps) {
        if (this.state.user.id !== nextProps.params.userId) {
            this.setUserState();
        }
    }

    setUserState() {
        this.setState({
            user: this.findUserById(this.props.params.userId)[0]
        });
    }

    findUserById(id) {
        return userlist.filter((item) => item.id === id);
    }

    render() {
        return (
            <div>
                <div>
                    <hr />
                    This is what we know:
                    <br />ID: {this.state.user.id}
                    <br />NAME: {this.state.user.name}
                </div>
            </div>
        );
    }
}

export class UserImage extends React.Component {
    constructor(props) {
        super(props);
        this.state = { user: { id: 0, name: '', img: '' } };
    }

    componentDidMount() {
        this.setUserState();
    }

    componentWillUpdate(nextProps) {
        if (this.state.user.id !== nextProps.params.userId) {
            this.setUserState();
        }
    }

    setUserState() {
        this.setState({
            user: this.findUserById(this.props.params.userId)[0]
        });
    }

    findUserById(id) {
        return userlist.filter((item) => item.id === id);
    }

    render() {
        return (
            <div>
                <div>
                    <hr />
                    This is what we know:
                    <img src={this.state.user.img} alt="Rambo"/>
                </div>
            </div>
        );
    }
}


export class Users extends React.Component {
    constructor(props) {
        super(props);
        this.state = { users: userlist };
    }

    componentWillMount() {
        if ('users' in this.props) {
            this.setState({ users: this.props.users });
        }
    }

    render() {
        return (
            <div>
                <h1>User List</h1>
                <div className="master">
                    <ul>
                        {this.state.users // eslint-disable-line newline-per-chained-call
                            .map((user) => (
                                <li key={user.id}><Link to={`/users/${user.id}`}>{user.name}</Link></li>
                            ))}
                    </ul>
                </div>
                <div className="detail">

                    {this.props.children}
                </div>
            </div>
        );
    }
}

