			"preview": "Новое цветное МФУ формата A4 <a href=\"http://www.xcom-shop.ru/oki_mc563dn_586013.html\">OKI MC563dn</a> печатает со скоростью до 30 страниц в минуту и поддерживает разрешение 1200 x 1200 dpi. Производитель обращает внимание на мощную систему безопасности, включающую фирменную функцию Private Print. МФУ ориентировано на предприятия малого и среднего бизнеса, а также рабочие группы любых размеров."
