			"pk_publication": "1868",
			"name": "Logitech G413: точные механические переключатели и красная подсветка",
			"name_url": "logitech_g413_tochnye_mehanicheskie_pereklyuchateli_ikrasnaya_podsvetka",
			"dtime": "1501448400",
			"dtime_show": "0",
			"tags": "Logitech, клавиатуры",
			"content": "<p>В продажу поступила новая клавиатура <a href=\"https://www.xcom-shop.ru/logitech_g413__mechanical_582994.html\">Logitech G413</a>. Она была разработана для тех, кто много времени проводит за играми. Для этого ее оснастили прецизионными механическими переключателями Romer-G, встроенное подсветкой и легким, но прочным корпусом из алюминия.</p>\r\n<p><h2>Механические клавиши</h2></p>\r\n<p>Сердцем <a href=\"https://www.xcom-shop.ru/logitech_g413__mechanical_582994.html\">Logitech G413</a> стали механические переключатели Romer-G. Они обеспечивают рабочие характеристики профессионального уровня, быстрый отклик и высочайшая надежность. Благодаря небольшому расстоянию до точки срабатывания (1,5 мм) команды выполняются на 25 % быстрее, чем у стандартных механических переключателей. Romer-G обеспечивают идеальное сочетание скорости, точности и бесшумности. </p>\r\n<div align=\"center\">\r\n<a href=\"https://www.xcom-shop.ru/logitech_g413__mechanical_582994.html\"><img src=\"/var/files/3b/bd/597ecf03bbdba114972857\" alt=\"Клавиатура проводная Logitech G413 Mechanical\" title=\"Клавиатура проводная Logitech G413 Mechanical\" /></a>\r\n<p style=\"font-size: 9px\">\r\nКлавиатура проводная Logitech G413 Mechanical\r\n</p>\r\n</div>\r\n<p>Клавиатура поставляется с 12 дополнительными колпачками. При необходимости вы сможете заменить колпачки на часто используемых клавишах, получив неоспоримое преимущество перед своими соперниками. Колпачки заменяются быстро и легко и помощью специального зажима, предназначенного для снятия колпачков Romer-G. Зажим входит в комплект поставки клавиатуры. </p>\r\n<p><h2>Точно направленная подсветка для клавиш</h2></p>\r\n<p>Переключатели Romer-G сконструированы так, чтобы свет точно и равномерно попадал на каждую клавишу. Благодаря этому свет не рассеивается и клавиши легко различить, особенно во время ночных игровых сессий. </p>\r\n<p><h2>Алюминиевый сплав авиационного класса</h2></p>\r\n<div align=\"center\">\r\n<a href=\"https://www.xcom-shop.ru/logitech_g413__mechanical_582994.html\"><img src=\"/var/files/67/9d/597ecf1679d82247064329\" alt=\"Клавиатура проводная Logitech G413 Mechanical\" title=\"Клавиатура проводная Logitech G413 Mechanical\" /></a>\r\n<p style=\"font-size: 9px\">\r\nКлавиатура проводная Logitech G413 Mechanical\r\n</p>\r\n</div>\r\n<p>Каркас <a href=\"https://www.xcom-shop.ru/logitech_g413__mechanical_582994.html\">Logitech G413</a> сделан из алюминиево-магниевого сплава 5052. Клавиатура была задумана, разработана и сконструирована очень скрупулезно и с большим вниманием к деталям. Результатом усилий специалистов стала модель, в которой есть именно то, что нужно пользователю. В нижней части устройства имеются удобные направляющие каналы для шнуров мыши и гарнитуры. Клавиатура снабжена ножками регулируемой высоты с резиновыми накладками, которые придадут вам уверенность во время напряженной игры. </p>\r\n<p><h2>Расширенные функции и программируемые макросы</h2></p>\r\n<p><a href=\"https://www.xcom-shop.ru/logitech_g413__mechanical_582994.html\">Logitech G413</a> поддерживает игровой режим, многоклавишные сочетаний (до 26 клавиш) и функцию устранения несрабатывания, которая помогает контролировать ситуацию, когда приходится подавать несколько команд в одно и то же время. А с помощью программы Logitech Gaming Software можно задать функции и макросы клавишам F1-F12. </p>",
			"fk_user": "shaxtep@gmail.com",
