			"name_url": "sandisk_ultra_dualm30_mgnovenno_osvobodite_mesto_vsvoem_android-smartfone",
			"dtime": "1501448400",
			"dtime_show": "0",
			"tags": "SanDisk, накопители, флешки",
			"content": "<p>Кончилось свободное место на Android-смартфоне? На решение этой проблемы придется потратить немало времени, особенно если ваше устройство не поддерживает карты microSD. Но флешка <a href=\"http://www.xcom-shop.ru/search/?o=n&s=SanDisk+SDDD3+G46\">SanDisk Ultra Dual m3.0</a> (серия SanDisk SDDD3 G46) упрощает перенос контента с телефона в компьютер. Этот удобный компактный накопитель с одной стороны имеет micro-USB, а с другой – USB 3.0, что позволяет легко перемещать контент между всеми вашими устройствами: со смартфона или планшета Android на ноутбук, ПК или Mac. Разъем USB 3.0 обладает повышенной скоростью и обратной совместимостью с портами USB 2.0.</p>\r\n<div align=\"center\">\r\n<a href=\"http://www.xcom-shop.ru/search/?o=n&s=SanDisk+SDDD3+G46\"><img src=\"/var/files/2a/2b/597ed4c2a2b5d540359154\" alt=\"Накопители SanDisk Ultra Dual m3.0\" title=\"Накопители SanDisk Ultra Dual m3.0\" /></a>\r\n<p style=\"font-size: 9px\">\r\nНакопители SanDisk Ultra Dual m3.0\r\n</p>\r\n</div>\r\n<p>Накопители <a href=\"http://www.xcom-shop.ru/search/?o=n&s=SanDisk+SDDD3+G46\">SanDisk Ultra Dual m3.0</a> представлены в версиях емкостью <a href=\"http://www.xcom-shop.ru/sandisk_sddd3-016g-g46_560812.html\">16 ГБ</a>, <a href=\"http://www.xcom-shop.ru/sandisk_sddd3-032g-g46_560809.html\">32 ГБ</a>, <a href=\"http://www.xcom-shop.ru/sandisk_sddd3-128g-g46_560795.html\">128 ГБ</a> и <a href=\"http://www.xcom-shop.ru/sandisk_sddd3-256g-g46_583464.html\">256 ГБ</a>. По данным производителя, при подключении через интерфейс USB 3.0 скорость чтения с флешки составляет до 150 МБ/с (до 130 МБ/с для младшей модели емкостью 16 ГБ).</p>\r\n<div align=\"center\">\r\n<a href=\"http://www.xcom-shop.ru/search/?o=n&s=SanDisk+SDDD3+G46\"><img src=\"/var/files/17/e4/597ed4d17e4e8254949782\" alt=\"Накопители SanDisk Ultra Dual m3.0\" title=\"Накопители SanDisk Ultra Dual m3.0\" /></a>\r\n<p style=\"font-size: 9px\">\r\nНакопители SanDisk Ultra Dual m3.0\r\n</p>\r\n</div>\r\n<p>Дополнительно упростить перенос файлов поможет фирменное приложение <a href=\"https://play.google.com/store/apps/details?id=com.sandisk.mz&hl=ru\">SanDisk Memory Zone</a> для Android (доступно в Google Play). Оно позволяет управлять памятью и контентом устройства, группировать фото, музыку и другой контент. Приложение SanDisk Memory Zone представляет собой удобное средство систематизации, просмотра и резервного копирования файлов для мобильных устройств на базе Android.</p>\r\n<div align=\"center\"><iframe width=\"600\" height=\"338\" src=\"https://www.youtube.com/embed/1Zs3VU9A298?rel=0&amp;showinfo=0\" frameborder=\"0\" allowfullscreen></iframe></div>\r\n<p>Также приложение SanDisk Memory Zone может автоматически перемещать файлы с устройства на флеш-накопитель <a href=\"http://www.xcom-shop.ru/search/?o=n&s=SanDisk+SDDD3+G46\">SanDisk Ultra Dual m3.0</a> для освобождения места.</p>",
			"fk_user": "shaxtep@gmail.com",
			"image": "14/99/597ed4e1499e8898139139",
			"preview": "Накопители <a href=\"http://www.xcom-shop.ru/search/?o=n&s=SanDisk+SDDD3+G46\">SanDisk Ultra Dual m3.0</a> с двумя интерфейсами (USB 3.0 и micro-USB) упрощают перенос контента с телефона в компьютер. Сэкономить время помогает и фирменное приложение SanDisk Memory Zone для Android. При подключении через интерфейс USB 3.0 скорость чтения составляет до 150 МБ/с."
