			"dtime_show": "0",
			"tags": "A4Tech, мышки",
			"content": "<p>В продажу поступили новые игровые мышки <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p81_578479.html\">A4Tech Bloody P81</a> и <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p85_578480.html\">A4Tech Bloody P85</a>. Они предлагают выгодное сочетание цены и характеристик, а также во всех смыслах ярких внешний вид.</p>\r\n<p><h2>A4Tech Bloody P81</h2></p>\r\n<p>Начнем с младшей модели <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p81_578479.html\">A4Tech Bloody P81</a>. Новинка получила эргономичную форму, семь программируемых клавиш и колесо прокрутки. Игровая мышь оснащена надежными оптическими минипереключателями Light Strike, рассчитанными на 20 млн. срабатываний. Они отличаются рекордно низким временем отклика — всего 0,2 мс.</p>\r\n<div align=\"center\">\r\n<a href=\"http://www.xcom-shop.ru/a4tech_bloody_p81_578479.html\"><img src=\"/var/files/2a/dc/597deeb2adc33266351170\" alt=\"Мышь A4Tech Bloody P81\" title=\"Мышь A4Tech Bloody P81\" /></a>\r\n<p style=\"font-size: 9px\">\r\nМышь A4Tech Bloody P81\r\n</p>\r\n</div>\r\n<p>В основе <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p81_578479.html\">A4Tech Bloody P81</a> лежит хорошо зарекомендовавший себя оптический сенсор Pixart PMW3325. Максимальное разрешение составляет 5000 CPI с возможностью отслеживания со скоростью до 100 дюймов в секунду и ускорением до 20 G.</p>\r\n<div align=\"center\">\r\n<a href=\"http://www.xcom-shop.ru/a4tech_bloody_p81_578479.html\"><img src=\"/var/files/01/61/597deec01618e838278323\" alt=\"Мышь A4Tech Bloody P81\" title=\"Мышь A4Tech Bloody P81\" /></a>\r\n<p style=\"font-size: 9px\">\r\nМышь A4Tech Bloody P81\r\n</p>\r\n</div>\r\n<p>Среди дополнительных особенностей <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p81_578479.html\">A4Tech Bloody P81</a> следует отметить индивидуальную RGB-подсветки. Для тонкой настройки следует воспользоваться фирменным программным обеспечением Bloody 6, которое позволяет выбрать один из 16,8 миллиона цветовых вариантов. Изначально предустановлено пять видов типов RGB-подсветки, однако владелец может создать свой световой эффект, а также сохранить его и поделиться им с друзьями. Также в устройстве реализована подсветка логотипа при включенном состоянии.</p>\r\n<p><h2>A4Tech Bloody P85</h2></p>\r\n<p>Второй интересной новинкой от компании A4Tech является <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p85_578480.html\">A4Tech Bloody P85</a>. С технической точки зрения она аналогична <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p81_578479.html\">A4Tech Bloody P81</a>, в основном отличается только внешность.</p>\r\n<div align=\"center\">\r\n<a href=\"http://www.xcom-shop.ru/a4tech_bloody_p85_578480.html\"><img src=\"/var/files/ad/93/597deecad9381949613522\" alt=\"Мышь A4Tech Bloody P85\" title=\"Мышь A4Tech Bloody P85\" /></a>\r\n<p style=\"font-size: 9px\">\r\nМышь A4Tech Bloody P85\r\n</p>\r\n</div>\r\n<p>В <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p85_578480.html\">A4Tech Bloody P85</a> тоже применяется сенсор с разрешением до 5000 CPI и специальные переключатели, жизненный цикл которых составляет более 20 миллионов кликов. Bloody P85 приятно удивит игроков своей исключительной эргономичностью, что обеспечит максимальный уровень комфорта во время игры.</p>\r\n<p>В оснащение <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p85_578480.html\">A4Tech Bloody P85</a> также входит восемь полноценных кнопок, которые можно запрограммировать с учетом особенностей и стиля игры. Доступно создание макросов, что очень удобно во многих онлайн-играх. Одним из главных преимуществ новой мышки является возможность быстро использовать весь арсенал вооружения в игре и реализовывать нескольких команд в одно нажатие, например, выстрел, бросок заклинания, стрейф и/или прыжок. Кнопку можно запрограммировать на выполнение любого действия или на автоматическое цикличное действие. Каждая клавиша обладает коротким и отчетливым ходом.</p>\r\n<div align=\"center\">\r\n<a href=\"http://www.xcom-shop.ru/a4tech_bloody_p85_578480.html\"><img src=\"/var/files/81/61/597deed8161ad213336484\" alt=\"Мышь A4Tech Bloody P85\" title=\"Мышь A4Tech Bloody P85\" /></a>\r\n<p style=\"font-size: 9px\">\r\nМышь A4Tech Bloody P85\r\n</p>\r\n</div>\r\n<p><a href=\"http://www.xcom-shop.ru/a4tech_bloody_p85_578480.html\">A4Tech Bloody P85</a> обладает одним из самых быстрых откликов в мире. Инновационная технология LK (Light Strike) использует оптические микропереключатели, обеспечивающие непревзойденное время отклика до 0,2 мс. А в сочетании с высокой производительностью процессора все это обеспечивает пользователю снайперскую прицельность и возможность проведения внезапных атак.</p>\r\n<p>В случае <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p85_578480.html\">A4Tech Bloody P85</a> тоже не обошлось без RGB-подсветки с пятью стандартными эффектами. Можно выбрать индивидуальный стиль: он настраивается с помощью ПО Bloody 6 и имеет 16,8 миллионов цветовых вариантов.</p>",
			"fk_user": "shaxtep@gmail.com",
