			"preview": "Новые игровые мышки <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p81_578479.html\">A4Tech Bloody P81</a> и <a href=\"http://www.xcom-shop.ru/a4tech_bloody_p85_578480.html\">A4Tech Bloody P85</a> предлагают выгодное сочетание цены и характеристик. Изюминкой является ярких во всех смыслах внешний вид: встроена RGB-подсветка, поддерживающая 16,8 миллионов цветовых вариантов."
